import { Component, HostBinding } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';

import { MatDialog } from '@angular/material/dialog';

import { OverlayContainer } from '@angular/cdk/overlay';

import { driver } from "driver.js";
//import "driver.js/dist/driver.css";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'portfolio';




  @HostBinding('class') className = '';



  toggleControl = new UntypedFormControl(false);
  constructor(private dialog: MatDialog, private overlay: OverlayContainer) { }
  ngOnInit(): void {

    this.toggleControl.valueChanges.subscribe((darkMode) => {

      const darkClassName = 'darkMode';

      this.className = darkMode ? darkClassName : '';
      console.log(this.className)

      if (darkMode) {

        this.overlay.getContainerElement().classList.add(darkClassName);

      } else {

        this.overlay.getContainerElement().classList.remove(darkClassName);

      }

    });

    const driverObj = driver({
      showProgress: true,
      steps: [
      //   { element: '.homecard', popover: { title: 'Welcome. Thanks for coming. Here is a short tour of my profile',side:'top',align:'end' } },
      //   { element: '.ToggleBtn', popover: { title: 'Here you can switch between dark and light theme' } },
      //   { element: '.container-fluid', popover: { title: 'My Skill Set',  description:'You can scroll the slider to see more'  } },
         { element: '.aboutme', popover: { title: 'My Professional Work Experience' } },
      { element: '.educational1', popover: { title: 'Qualifications',side:'top',align:'end' } },
      { element: '.projects', popover: { title: 'Some of my works', description: 'You can click on the "click to view" to view more'  } },
       { element: '.skills1', popover: { title: 'Certifications ',side:'top',align:'end' } },
       { element: '.thanks', popover: { title: 'Thank you!',description:'Once again thank you for showing interest in my profile.' } },
      ]
    });
    driverObj.drive();
    const container = document.getElementById("main");
    container.scroll(0, 0);
  }



  showDialog(): void {

   
  }


}
