import { Component, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import Typewriter from 'typewriter-effect/dist/core';
import { driver } from "driver.js";
//import "driver.js/dist/driver.css";

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.scss']
})
export class HomepageComponent implements OnInit {
  public form: UntypedFormGroup;
  rating3: number;
  
  ngAfterViewInit(){
    
    var app = document.querySelector('.tw');
 console.log(app)
    var typewriter = new Typewriter(app, {
      loop: true,
      delay: 100,
    });
     
    typewriter
      .pauseFor(500)
      .typeString(' I am a <span style="color: #27ae60;">Software</span>')
      .pauseFor(800)
      .deleteChars(8)
      .typeString(' <span style="color: #27ae60;">MEAN stack</span>')
      .pauseFor(800)
      .deleteChars(10)
      .typeString(' <span style="color: #27ae60;">Web</span>' )
      .pauseFor(800)
      .deleteChars(3)
      .typeString(' <span style="color: #27ae60;">Hybrid mobile app</span> developer' )
      .pauseFor(800)
      .start();
  }
  constructor(private fb: UntypedFormBuilder) {
    this.rating3 = 0;
    this.form = this.fb.group({
      rating: [5],
    })
   }

  ngOnInit(): void {
   
  }
 
  
  
  
}
